.wrapper{
    margin: 10px;
    li{
        list-style: none;
    }
  }
  
  .wrapper .top_navbar{
    width: calc(100% - 20px);
    height: 60px;
    display: flex;
    position: fixed;
    top: 10px;
  }
  
  .wrapper .top_navbar .hamburger{
    width: 70px;
    height: 100%;
    background: #2e4ead;
    padding: 15px 17px;
    border-top-left-radius: 20px;
    cursor: pointer;
  }
  
  .wrapper .top_navbar .hamburger div{
    width: 35px;
    height: 4px;
    background: #92a6e2;
    margin: 5px 0;
    border-radius: 5px;
  }
  
  .wrapper .top_navbar .top_menu{
    width: calc(100% - 70px);
    height: 100%;
    background: #fff;
    border-top-right-radius: 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 20px;
    box-shadow: 0 1px 1px rgba(0,0,0,0.1);
  }
  
  .wrapper .top_navbar .top_menu .logo{
    color: #2e4ead;
    font-size: 20px;
    font-weight: 700;
    letter-spacing: 3px;
  }
  
  .wrapper .top_navbar .top_menu ul{
    display: flex;
  }
  
  .wrapper .top_navbar .top_menu ul li span{
    display: block;
    margin: 0 10px;
    width: 35px;
    height: 35px;
    line-height: 35px;
    text-align: center;
    border: 1px solid #2e4ead;
    border-radius: 50%;
    color: #2e4ead;
  }
  
  .wrapper .top_navbar .top_menu ul li span:hover{
    background: #4360b5;
    color: #fff;
  }
  
  .wrapper .top_navbar .top_menu ul li span:hover i{
    color: #fff;
  }
  
  .wrapper .sidebar{
    position: fixed;
    top: 70px;
    left: 10px;
    background: #2e4ead;
    width: 200px;
    height: calc(100% - 80px);
    border-bottom-left-radius: 20px;
    transition: all 0.3s ease;

        li{
            margin-left: -30px;
        }
    
  }
  
  .wrapper .sidebar ul li p{
      display: block;
      padding: 20px;
      color: #fff;
      position: relative;
      margin-bottom: 1px;
      color: #92a6e2;
      white-space: nowrap;
  }
  
  .wrapper .sidebar ul li p:before{
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 3px;
    height: 100%;
    background: #92a6e2;
    display: none;
  }
  
  .wrapper .sidebar ul li p span.icon{
    margin-right: 10px;
    display: inline-block;
  }
  
  .wrapper .sidebar ul li p span.title{
    display: inline-block;
  }
  
  .wrapper .sidebar ul li p:hover,
  .wrapper .sidebar ul li p.active{
    background: #4360b5;
    color: #fff;
  }
  
  .wrapper .sidebar ul li p:hover:before,
  .wrapper .sidebar ul li p.active:before{
    display: block;
  }
  
  .wrapper .main_container{
    width: 100%;
    margin-top: 70px;
    margin-left: 200px;
    padding: 15px;
    transition: all 0.3s ease;
  }
  
  .wrapper .main_container .item{
    background: #fff;
    margin-bottom: 10px;
    padding: 15px;
    font-size: 14px;
    line-height: 22px;
  }
  
  .wrapper.collapseMenu .sidebar{
    width: 70px;
  }
  
  .wrapper.collapseMenu .sidebar ul li p{
    text-align: center; 
  }
  
  .wrapper.collapseMenu .sidebar ul li p span.icon{
    margin: 0;
  }
  
  .wrapper.collapseMenu .sidebar ul li p span.title{
    display: none;
  }
  
  .wrapper.collapseMenu .main_container{
    width: calc(100% - 70px);
    margin-left: 70px;
  }